
* {
  padding: 0;
  margin: 0;
}
html, body {
  width: 100%;
  height: 100%;
}

.chart-container {
  position: fixed;
  top: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 80%;
  height: 70%;
  border: 2px solid #ccc;
  border-radius: 10px;
}
.echarts {
  width: 100% !important;
  height: 100% !important;
}

.echarts {
  width: 600px;
  height: 400px;
}
